body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  background: linear-gradient(to left, rgb(155, 9, 9) 0%, rgb(36, 16, 2) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    src: local('SEGA LOGO FONT'), url(https//mikeDavidson20.github.io/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    color:rgba(238, 96, 93, 0.977);
}
